import React, { Component } from 'react';
import ListItems from "./ListItems";
import './App.css';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      items: [],
      edit: false,
      buttonText: "Add",
      editKey: null
    };
    this.addItem = this.addItem.bind(this);
    this.deleteItem = this.deleteItem.bind(this);
    this.edit = this.edit.bind(this);
  }

  addItem(e) {
    if (this._inputElement.value !== "") {
      if (this.state.edit) {
        var x = this.state.editKey;
        var editItem = this.state.items.filter(function (item) {
          return (item.key === x)
        });
        editItem[0].text = this._inputElement.value;
        this.setState((prevState) => {
          return {
            items: prevState.items,
            edit: false,
            buttonText: "Add",
            editKey: null
          }
        });
        this._inputElement.value = "";
        this.forceUpdate();
      } else {
        var listItem = {
          text: this._inputElement.value,
          key: Date.now()
        };
        this.setState((prevState) => {
          return {
            items: prevState.items.concat(listItem),
            edit: prevState.edit,
            buttonText: prevState.buttonText,
            editKey: prevState.editKey
          };
        });
        this._inputElement.value = "";
      }
    }
    e.preventDefault();
  }

  deleteItem(key) {
    var newItems = this.state.items.filter(function (item) {
      return (item.key !== key)
    });
    this.setState({
      items: newItems,
      edit: this.state.edit,
      buttonText: this.state.buttonText,
      editKey: this.state.editKey
    });
  }

  edit(key) {
    var editItem = this.state.items.filter(function (item) {
      return (item.key === key)
    });
    this._inputElement.value = editItem[0].text;
    this.setState({
      items: this.state.items,
      edit: true,
      buttonText: "Edit",
      editKey: key
    });
    this.forceUpdate();
  }

  render() {
    return (
      <div className="App" id="app">

        <div className="App-header">
          <h2>To Do List</h2>
        </div>

        <form onSubmit={this.addItem}>
          <div className="input-group" id="formGroup">
            <input type="text" className="form-control" placeholder="Enter a task" ref={(a) => this._inputElement = a} id="appInput" />
            <span className="input-group-btn">
              <button className="btn btn-light" id="submitButton" type="button" onClick={this.addItem}>{this.state.buttonText}</button>
            </span>
          </div>
        </form>

        <div>
          <ListItems entries={this.state.items} delete={this.deleteItem} edit={this.edit} />
        </div>

      </div>
    );
  }
}

export default App;



