import React, { Component } from "react";

class ListItems extends Component {
    constructor(props) {
        super(props);
        this.createItem = this.createItem.bind(this);
    }
    createItem(item) {
        return <li key={item.key} className="list-group-item">{item.text}<div className="delete" onClick={() => this.delete(item.key)}></div><div className="edit" onClick={() => this.edit(item.key)}></div></li>
    }
    delete(key) {
        this.props.delete(key);
    }
    edit(key) {
        this.props.edit(key);
    }
    render() {
        var listItems = this.props.entries.map(this.createItem);
        return (
            <ul className="list-group">
                {listItems}
            </ul>
        );
    }
}

export default ListItems;


